.progressBarStars {
  position: relative;
  z-index: 256;
  margin-left: 0.8%;
  height: 102%;
  animation-duration: 0.5s;
  animation-name: animate-pop;
  animation-timing-function: cubic-bezier(0.26, 0.53, 0.74, 1.48);
}

.starspos {
  top: 6.5%;
  left: 76.8%;
  height: 7%;
  position: fixed;
  z-index: 257;
  width: 23%;
}

@keyframes animate-pop {
  0% {
    opacity: 0;
    transform: scale(0.5, 0.5);
  }

  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}
